<template>
  <v-app id="inspire">
    <v-container grid-list-xl text-xs-center>
      <v-layout row wrap>

        <v-flex xs10 offset-xs1>
          <v-card-text style="color:#546E7A;font-size:20px">Преобразование скорости с км/ч м/с</v-card-text>
        </v-flex>

        <v-flex xs10 offset-xs1>
          <v-card-text style="color:#546E7A;font-size:15px">км/ч</v-card-text>
          <input v-model="speedK" placeholder="km/h" style="border: solid 1px #009688">
        </v-flex>

        <v-flex xs10 offset-xs1>
          <v-card-text style="color:#546E7A;font-size:15px">м/с</v-card-text>
          <input v-if="!checkInput" v-model="mutateVal" placeholder="m/s" readonly style="border: solid 1px #009688">
          <v-card-text v-else style="color:#D32F2F;font-size:20px">Введите числовое значение!</v-card-text>
        </v-flex>

      </v-layout>
    </v-container>

    <v-divider></v-divider>

    <v-container grid-list-xl text-xs-center>
      <v-layout row wrap>

        <v-flex xs10 offset-xs1>
          <v-card-text style="color:#546E7A;font-size:20px">Кто вы по гороскопу?</v-card-text>
          <v-card-text style="color:#546E7A;font-size:14px">введите дату рождения дд/мм</v-card-text>
        </v-flex>

        <v-flex xs10 offset-xs1>
          <input v-model="date" placeholder="01.01" style="border: solid 1px #009688">
          <v-card-text v-if="sign" style="color:#D32F2F;font-size:20px"> {{sign}}</v-card-text>
        </v-flex>

        <v-flex xs10 offset-xs1>
          <v-btn v-show="isActive" @click="yourHoroscope">ok</v-btn>
        </v-flex>

      </v-layout>
    </v-container>

    <v-divider></v-divider>

    <dialogWindow></dialogWindow>

  </v-app>
</template>

<script>
  import dialogWindow from './view/dialog.vue'
  export default {
    components: {
      dialogWindow
    },
    data() {
      return {
        speedK: '',
        date: '',
        sign: ''
      }
    },
    computed: {
      // Преобразовываем скорость с км/ч на м/с
      mutateVal() {
        if (this.speedK) {
          return Math.round((this.speedK * 5 / 18) * 100) / 100
        }
      },
      checkInput() {
        return this.speedK && isNaN(this.speedK)
      },
      isActive() {
        return this.date;
      },
    },
    methods: {
      yourHoroscope() {
        let gapDate = this.date.split('.');
        let day = Number(gapDate[0]);
        let month = Number(gapDate[1]);
        let value;
        if (month === 2 && day >= 19 || month === 3 && day <= 20) value = "Рыбы";
        else if (month === 1 && day >= 20 || month === 2 && day <= 18) value = "Водолей";
        else if (month === 3 && day >= 21 || month === 4 && day <= 19) value = "Овен";
        else if (month === 4 && day >= 20 || month === 5 && day <= 20) value = "Телец";
        else if (month === 5 && day >= 21 || month === 6 && day <= 21) value = "Близнецы";
        else if (month === 6 && day >= 22 || month === 7 && day <= 22) value = "Рак";
        else if (month === 7 && day >= 23 || month === 8 && day <= 22) value = "Лев";
        else if (month === 8 && day >= 23 || month === 9 && day <= 22) value = "Дева";
        else if (month === 9 && day >= 23 || month === 10 && day <= 22) value = "Весы";
        else if (month === 10 && day >= 23 || month === 11 && day <= 21) value = "Скорпион";
        else if (month === 11 && day >= 22 || month === 12 && day <= 21) value = "Стрелец";
        else if (month === 12 && day >= 22 || month === 1 && day <= 19) value = "Козерог";
        else value = "Неверная дата!";
        if ((day < 1) || (month == 2 && day > 29)) value = "Неверная дата!";

        if ((month === 1 || month === 3 || month === 5 || month === 7 || month === 8 || month === 10 || month === 12) && day > 31) value = "Неверная дата!";

        if ((month === 4 || month === 6 || month === 9 || month === 11) && (day > 30)) value = "Неверная дата!";

        this.date = ''
        this.sign = value
      }
    }
  }
</script>

