<template>
    <v-app id="inspire">
        <v-container v-if="!dialogWindow" fluid fill-height>
            <v-layout align-center justify-center>

                <v-flex xs12 sm8 md5>
                    <v-card class="elevation-12">

                        <v-toolbar dark color="primary">
                            <v-toolbar-title>Вход</v-toolbar-title>
                            <v-spacer></v-spacer>
                        </v-toolbar>

                        <v-card-text>
                            <v-form v-model="valid">
                                <v-text-field name="login" label="Login" type="email" required v-model="email" :rules="emailRules"></v-text-field>
                                <v-text-field id="password" name="password" label="password" type="password" required v-model="password" :rules="passwordRules"></v-text-field>
                            </v-form>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="primary" @click.prevent="signin" :disabled="!valid">Войти</v-btn>
                        </v-card-actions>

                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>

        <v-container v-else grid-list-xl text-xs-center>
            <v-layout row wrap>

                <v-flex xs10 offset-xs1>
                    <v-card-text style="color:#D32F2F;font-size:20px"> Вход выполнен - {{this.email}}</v-card-text>
                </v-flex>

                <v-flex xs10 offset-xs1>
                    <v-btn @click="signin" >Вернуть форму входа</v-btn>
                </v-flex>

            </v-layout>
        </v-container>

    </v-app>
</template>

<script>
    export default {
        data() {
            return {
                dialogWindow: false,
                email: null,
                password: null,
                valid: false,
                emailRules: [
                    (v) => !!v || 'Пожалуйста введите е-мейл', // Если пустое поле
                    (v) => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'Неправильный емейл' // Стандартный регуляр экспрешшн
                ],
                passwordRules: [
                    (v) => !!v || 'Пожалуйста введите пароль',
                    (v) => (v && v.length >= 6) || 'Пароль слишком короткий-минимум 6 символов'
                ],
            }
        },
        methods: {
            signin() {
                this.dialogWindow = !this.dialogWindow;
                this.password = '';
            }
        }
    }
</script>

